    font-family: 'montserrat', sans-serif;
    background-color: #f4f4f9;
}

@media screen and (max-width: 767px) {
    .section {
        padding-top: 30px;
        padding-bottom: 30px;
    }
}

@media screen and (min-width: 768px) {
    .section {
        padding-top: 50px;
        padding-bottom: 50px;
    }
}

.container {
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 767px) {
    .container {
        max-width: 480px;
        padding-left: 20px;
        padding-right: 20px;
    }
}

@media screen and (min-width: 768px) {
    .container {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }
}

@media screen and (min-width: 1280px) {
    .container {
        width: 1280px;
        padding-left: 16px;
        padding-right: 16px;
    }
}

.users-title {
    display: block;
    text-align: center;
    text-transform: uppercase;
}

.filter-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.filter-container input {
    margin: 0 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 200px;
}

.users {
    display: block;
    padding-left: 0px;

    @media screen and (min-width: 768px) {
        display: grid;
        grid-template-columns: repeat(2, minmax(275px, 1fr));
        grid-gap: 2rem;
    };

    @media screen and (min-width: 1278px) {
        grid-template-columns: repeat(3, minmax(275px, 1fr));
    };
}

.list {
    list-style: none;
}

.card {
    margin-left: auto;
    margin-right: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* width: 350px; */
    overflow: hidden;
    transform: scale(1);
    transition: transform 0.3s ease;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.05);
}

.user-header {
    background-color: #007bff;
  padding: 20px;
  color: #fff;
}

.user-title {
    font-size: 1.5rem;
        margin: 0;
}

.username {
    font-size: 1rem;
        margin-top: 5px;
        font-style: italic;
}

.user-body {
    padding: 20px;
}

.user-body p {
    margin: 10px 0;
}

.user-body a {
    color: #007bff;
    text-decoration: none;
}

.address,
.company {
    margin-top: 20px;
}

h3 {
    margin-bottom: 10px;
    color: #333;
    font-size: 1.2rem;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,qCAAqC;IACrC,yBAAyB;AAC7B;;AAEA;IACI;QACI,iBAAiB;QACjB,oBAAoB;IACxB;AACJ;;AAEA;IACI;QACI,iBAAiB;QACjB,oBAAoB;IACxB;AACJ;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI;QACI,gBAAgB;QAChB,kBAAkB;QAClB,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,YAAY;QACZ,kBAAkB;QAClB,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,aAAa;QACb,kBAAkB;QAClB,mBAAmB;IACvB;AACJ;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,iBAAiB;;IAEjB;QACI,aAAa;QACb,oDAAoD;QACpD,cAAc;IAClB;;IAEA;QACI,oDAAoD;IACxD;AACJ;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,sBAAsB;IACtB,mBAAmB;IACnB,wCAAwC;IACxC,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,+BAA+B;IAC/B,eAAe;AACnB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,yBAAyB;EAC3B,aAAa;EACb,WAAW;AACb;;AAEA;IACI,iBAAiB;QACb,SAAS;AACjB;;AAEA;IACI,eAAe;QACX,eAAe;QACf,kBAAkB;AAC1B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;;IAEI,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;IACnB,WAAW;IACX,iBAAiB;AACrB","sourcesContent":["body {\n    font-family: 'montserrat', sans-serif;\n    background-color: #f4f4f9;\n}\n\n@media screen and (max-width: 767px) {\n    .section {\n        padding-top: 30px;\n        padding-bottom: 30px;\n    }\n}\n\n@media screen and (min-width: 768px) {\n    .section {\n        padding-top: 50px;\n        padding-bottom: 50px;\n    }\n}\n\n.container {\n    margin-left: auto;\n    margin-right: auto;\n}\n\n@media screen and (max-width: 767px) {\n    .container {\n        max-width: 480px;\n        padding-left: 20px;\n        padding-right: 20px;\n    }\n}\n\n@media screen and (min-width: 768px) {\n    .container {\n        width: 768px;\n        padding-left: 32px;\n        padding-right: 32px;\n    }\n}\n\n@media screen and (min-width: 1280px) {\n    .container {\n        width: 1280px;\n        padding-left: 16px;\n 